body {
  margin: 0;
  padding: 0;
  background: #1A2238;
  color: #FAFAFA;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}



@media only screen and (min-width: 780px) {
    header{
        width: 700px;
        margin: 0 auto;
    }
    .TodoList-container{
        width: 700px;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
    }
    .LoadingTodo-container{
        width: 700px;
    }
    .TodoCongrats{
        width: 500px;
        margin: 0 auto;
        margin-top: 20px;
    }
    .TodoItem{
        width: 300px;
        margin: 0 auto;
        display: flex;
    }
    .empty-todos--p{
        margin: 0 auto;
        margin-top: 20px;
    }
    
}
.TodoCounter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
  }

  .TodoCounter--loading{
    opacity: 25%;
  }

.TodoSearch {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:-ms-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-color: #9DAAF2;
  }

  .TodoSearch:disabled{
    opacity: 25%;
  }
.TodoList-container{
  padding-bottom: 200px;
}

ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
  }

p{
  text-align: center;
  margin-top: 30px;
}
.Icon-container{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
}

.Icon-container--check{
    position: absolute;
    left: 12px;
}

.Icon-container--delete{
    position: absolute;
    right: 0;
}

.Icon.svg{
    width: 24px;
    height: 24px;
    transition: fill .15s;
}

.Icon-container:hover .Icon.svg--check{
    fill: #4caf50;
}

.Icon-container:hover .Icon-svg--delete{
    fill: red;
}

.Icon-container-hide{
    display: none;
}
.TodoItem {
    background-color: #FAFAFA;
    color: #1A2238;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(128, 129, 129, 0.719);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .TodoItem-p--hide {
   display: none;
  }


form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px, solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100%-25px);
  }
  
  textarea::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea:-ms-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea::placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #9ca9f2 ;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button-add {
    background-color: #9ca9f2;
    box-shadow: 0px 5px 25px rgba(156, 169, 242, 0.5);
  }
  
  .TodoForm-button-cancel {
    background: #FF6A3D;
  }
  
.CreateTodoButton {
    background-color: #9ca9f2;
    box-shadow: 0px 5px 25px rgba(187, 193, 223, 0.7);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
  }
  
  .CreateTodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }
.HideCompletedTodos {
    width: 220px;
    background-color: #FF6A3D;
    box-shadow: 0px 5px 25px rgba(247, 86, 23, 0.863);
    border: none;
    cursor: pointer;
    border-radius: 20px;
    font-size: 20px;
    position: fixed;
    bottom: 94px;
    left: 24px;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
    padding: 10px 20px;
    font-weight: bolder;
  }
  
  .HideCompletedTodos:hover {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }


.ShowCompletedTodos {
    width: 220px;
    background-color: #F4DB7D;
    box-shadow: 0px 5px 25px rgba(244, 219, 125, 0.7);
    border: none;
    cursor: pointer;
    border-radius: 20px;
    font-size: 20px;
    position: fixed;
    bottom: 24px;
    left: 24px;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
    padding: 10px 20px;
    font-weight: bolder;
  }
  
  .ShowCompletedTodos:hover {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }

  
.TodoCongrats{
    color: #9ca9f2;
    text-align: center;
    display: block;
}

.TodoCongrats span{
    color: #f4db7d;
}

.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }

.LoadingTodo-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text{
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    border-radius: 50px;
    height: 48px;
    width: 48px;
}

.LoadingTodo-completeIcon{
    position: absolute;
    left: 12px;
}

.LoadingTodo-deleteIcon{
    position: absolute;
    top: -24px;
    right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    background: linear-gradient(90deg, rgb(62, 67, 87), rgb(81, 91, 152));
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}

@keyframes loadingAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}
.empty-todos--p{
    font-size: 2rem;
}

.empty-todos--p span{
    color: #9ca9f2;
}
.EmptySearchedResults{
    color: #9ca9f2;
    text-align: center;
    display: block;
}

.EmptySearchedResults span{
    color: #ff6a3d;
}

.searchValue{
    font-weight: bold;
}
.ChangeAlert--container{
    background: rgba(32,35,41,.8);
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    flex-direction: column;
}

.ChangeAlert--container p{
    font-size: 1.4rem;
    font-weight: bolder;
}

.ChangeAlert--container button{
    width: 220px;
    margin-top: 10px;
    background-color: #FF6A3D;
    box-shadow: 0px 5px 25px rgba(247, 86, 23, 0.863);
    border: none;
    cursor: pointer;
    border-radius: 20px;
    font-size: 20px;
    color: #FAFAFA;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
    padding: 10px 20px;
    font-weight: bolder;
  }
  
  .ChangeAlert--container button:hover{
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }


